import React, { useState } from 'react';
import s from './AddOrder.module.css';
import '../../../css/main.css';
import '../../../css/normalize.css';
import { useHttp } from '../../../hooks/http.hookAddorder';


const AddOrder = () => {
    const {loading, request} = useHttp()
    const [form, setForm] = useState({
        category: '',
        title: '',
        description: '',
        cost: '',
        numberOfDays: ''
    })

    const orderHandler = e => {
        setForm({ ...form, [e.target.name]: e.target.value})
    }

    const handleOrderExecutor = async (e) => {
        try{
            e.preventDefault();
            const data = await request ('http://79.174.13.220:8080/orders/publish-order', 'POST', { ...form })
        }
        catch (e) {

        }
    }
    return(
        <section className={s.form}>
            <div className={s.container}>
                <p className={s.form__title}>  </p>
                <div className={s.container__form}>
                    <form className={s.feedback}>
                        <select name="category" id="category" onChange={orderHandler} disabled={loading} className={s.form__select}>
                            <option selected> </option>
                            <option value=""></option>
                            <option value="  ">  </option>
                            <option value="   ">   </option>
                            <option value=" "> </option>
                            <option value="  ">  </option>
                            <option value=""></option>
                            <option value="  ">  </option>
                            <option value="  ">  </option>
                            <option value="  IT">  IT</option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                        </select>
                        <input className={s.form__order} type="text" name="title" id="title" disabled={loading} onChange={orderHandler} placeholder=" :"/>
                        <textarea className={s.form__info} name="description" id="description" disabled={loading} onChange={orderHandler} placeholder=" :"></textarea>
                        <input className={s.form__cost} type="text" name="cost" id="cost" disabled={loading} onChange={orderHandler} placeholder=" *"/>
                        <input className={s.form__day} type="text" name="numberOfDays" id="numberOfDays" disabled={loading} onChange={orderHandler} placeholder="   :"/>
                        <button className={s.form__submit} type="submit" onClick={handleOrderExecutor}>  </button>
                    </form>
                </div>
            </div>
	    </section>
    );
}

export default AddOrder;