import React from 'react';
import s from './PayoutMoney.module.css';
import '../../../css/main.css';
import '../../../css/normalizee.css';

const PayoutMoney = () => {
    return(
        <section className={s.withdrawal}>
            <h2 className={s.withdrawal__title}> </h2>
            <div className={s.withdrawal__container}>
                <form action="#" className={s.withdrawal__form}>
                    <input className={s.withdrawal__sum} type="text" placeholder=" :"/>
                    <p className={s.withdrawal__variant}>    </p>
                    <input className={s.withdrawal__radio} type="radio" name=""/>
                    <label className={s.withdrawal__label}> </label><br/>
                    <div className={s.withdrawal__card}>
                        <p className={s['withdrawal__card-column']}>Visa, MasterCard, </p>
                        <p className={s['withdrawal__card-comission']}>: 2.5%</p>
                    </div>
                    <input className={s.withdrawal__radio} type="radio" name=""/>
                    <label className={s.withdrawal__label}> </label><br/>
                    <div className={s.withdrawal__card}>
                        <p className={s['withdrawal__card-column']}> </p>
                        <p className={s['withdrawal__card-comission']}>: 2.5%</p>
                    </div>
                    <button className={s.withdrawal__submit} type="submit"> </button>
                </form>
            </div>
	</section>
    )
}

export default PayoutMoney;