import React from 'react';
import s from './Registration.module.css';
import '../../css/main.css';
import { useState } from 'react';
import { useHttp } from '../../hooks/http.hook2';

const Registration = () => {
    const {loading, request} = useHttp()
    const [values, setValues] = useState({
        firstname: '',
        lastname: '',
        login: '',
        telephoneNumber: '',
        email: '',
        password: '',
        dateOfBirth: ''
    })


    const registerHandler = e => {
        setValues({ ...values, [e.target.name]: e.target.value })
    }

    const handleSubmitExecutor = async (e) => {
        try {
            e.preventDefault();
            const data = await request ('http://79.174.13.220:8080/users/sign-up-specialist', 'POST', { ...values }) //    
        }
        catch (e) {

        }
    }
    const handleSubmitCustomer= async (e) => {
        try {
            e.preventDefault();
            const data = await request ('http://79.174.13.220:8080/users/sign-up-client', 'POST', { ...values }) //    
        }
        catch (e) {

        }
    }

    return (
        <>
            <section className={s.registration}>
                <div className={s.registration__container}>
                    <h2 className={s.registration__title}> </h2>
                    <ul className={s.registration__list}>
                        <li className={s.registration__item}>
                            <a className={s.registration__link} href="#"></a>
                        </li>
                        <li className={s.registration__item}>
                            <a className={s.registration__link} href="#">Facebook</a>
                        </li>
                        <li className={s.registration__item}>
                            <a className={s.registration__link, s.registration__od} href="#"></a>
                        </li>
                        <li className={s.registration__item}>
                            <a className={s.registration__link} href="#">Google</a>
                        </li>
                    </ul>
                </div>
            </section>
            <section className={s.mail}>
                <div className={s.mail__container}>
                    <h2 className={s.mail__title}>  </h2>
                    <form className={s.mail__form}>
                        <input className={s.mail__input} type="text" id="login" name="login" value={values.login} onChange={registerHandler} placeholder="" />
                        <input className={s.mail__input} type="text" id="firstname" name="firstname" value={values.firstname} onChange={registerHandler} placeholder="" />
                        <input className={s.mail__input} type="text" id="lastname" name="lastname" value={values.lastname} onChange={registerHandler} placeholder="" />
                        <input className={s.mail__input} type="email" id="email" name="email" value={values.email} onChange={registerHandler} placeholder="e-mail" />
                        <input className={s.mail__input} type="text" id="telephoneNumber" name="telephoneNumber" value={values.telephoneNumber} onChange={registerHandler} placeholder="" />
                        <input className={s.mail__input} type="text" id="dateOfBirth" name="dateOfBirth" value={values.dateOfBirth} onChange={registerHandler} placeholder=" " />
                        {/* <input className={s.mail__input} type="text" id="country" name="country" value={values.country} onChange={registerHandler} placeholder=":" />
                        <input className={s.mail__input} type="text" id="city" name="city" value={values.city} onChange={registerHandler} placeholder=":" /> */}
                        <input className={s.mail__input} type="password" id="password" name="password" value={values.password} onChange={registerHandler} placeholder=":" />
                        {/* <input className={s.mail__input} type="password" id="password2" name="password2" value={values.password2} onChange={registerHandler} placeholder=" :" /> */}
                        <div>
                            <input className={s.mail__check} type="checkbox" />
                            <label>      </label>
                        </div>
                        <button className={s.mail__button} type="submit" onClick={handleSubmitExecutor}>  </button>
                        <button className={s.mail__button2} type="submit" onClick={handleSubmitCustomer}>  </button>
                    </form>
                    <p className={s.mail__info}>  ,     <br />       </p>
                </div>
            </section>
        </>
    );
}

export default Registration;

