import React from 'react';
import s from './DepositMoney.module.css';
import '../../../css/main.css';
import '../../../css/normalize.css';

const DepositMoney = () => {
    return(
        <section className={s.money}>
            <div className={s.money__container}>
                <form className={s.money__form}>
                    <h2 className={s.money__title}> </h2>
                    <input className={s.money__sum} type="text" placeholder=" :"/>
                    <p className={s.money__way}>  </p>
                    <input className={s.money__radio} type="radio" name=""/>
                    <label className={s.money__label}> </label><br/>
                    <div className={s.money__card}>
                        <p className={s['money__card-column']}>Visa, MasterCard, </p>
                        <p className={s['money__card-comission']}>: 2.5%</p>
                    </div>
                    <input className={s.money__radio} type="radio" name=""/>
                    <label className={s.money__label}> </label><br/>
                    <div className={s.money__card}>
                        <p className={s['money__card-column']}> </p>
                        <p className={s['money__card-comission']}>: 2.5%</p>
                    </div>
                    <button className={s.money__submit} type="submit"> </button>
                </form>
            </div>
	    </section>
    );
}

export default DepositMoney;